.Folder {
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: transparent;
    cursor: pointer;
}

.Icon {
    background-color: transparent;
    width: 15vh;
}

.text {
    margin-top: -1vh;
    background-color: transparent;
    font-size: 1vh;
}

.Folder:hover {
    background-color: rgb(187, 187, 187);
}
