.MyImage {
    display: inline-flex;
    max-width: 100%;
  }
  
  .Windows {
    display: flex;  
    margin: 0 auto;
  }
  
  .TopVar {
    display: flex;
    font-size: .8vh;
    color: #888687;
    background-color: #e6e7eb;
    padding: 1.2vh;
    padding-left: 10vh;
    padding-right: 10vh;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    cursor: pointer;
  }
  
  .ActionPoint {
    display: flex;
    align-items: flex-end;
    max-height: 1vh;
    background: none;
    font-size: 8vh;
    position: relative;
    right: 10vh;
    top: 1.8vh;
    cursor: pointer;
  }
  
  .FileName {
    display: flex;
    align-items: center;
    position: relative;
    right: 2vh;
    background-color: transparent;
    font-size: 1vh;
    max-height: 1vh;
    user-select: none;
  }
  
  .Cross {
    color: #ff634a;
  }
  
  .Minimize {
    color: #ffba18;
  }
  
  .Zoom {
    color: #2ea839;
  }
  
  .Content {
    background-color: #fff001;
    padding: 1vh;
    padding-left: 10vh;
    padding-right: 10vh;
    padding-bottom: 20vh;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  
  