
.Section4-hoover {
  color: rgb(32, 32, 32);
  font-size: 4vh;
  background: yellow;
  padding: 1vh;
  border-radius: 1vh;
  box-shadow: #eae5e2 1vh 1vh;
  margin-top: 9vh;
  margin-bottom: 4vh;
  border: solid black;
  width: 18vh;
  text-align: center;
  }

.contact {
  width: 100vh;
  margin-bottom: 17vh;
}  

.email {
  display: block;
  margin-left: 4.5%;
  margin-top: 1vh;
  color: white;
  background-color: black;
  font-size: 2vh;
  width: 50%;
  height: 4vh;
  padding: .5vh;
  border-radius: 1vh;
}

.TextContent {
  color: white;
  background-color: black;
  font-size: 2vh;
  margin-top: 2vh;
  width: 90%;
  height: 40vh;
  padding: .5vh;
  border-radius: 1vh;
  resize: none;
}

.submit {
  display: block;
  margin-left: 47%;
  color:black;
  background-color: #eae5e2;
  font-size: 2vh;
  font-weight: 700;
  width: 9%;
  height: 4vh;
  padding: .5vh;
  border-radius: 1vh;
  transition: transform .2s;
  margin-top: 1vh;
}

.submit:hover{
  transform: scale(1.01);
  cursor: pointer;
  background-color: pink;
}

footer{
  margin-top: 25vh;
  background-color: rgb(27, 27, 27);
  padding: 2vh;
  z-index: 10;
}

.FooterText{
  text-align: center;
  color: #eae5e2;
  background-color: transparent;
}
