.Section2 {
  color: white;
  display: flex;
  flex-direction: column;
  margin-top: 9vh;
}

.Section2-hoover {
  color: rgb(32, 32, 32);
  font-size: 4vh;
  background: yellow;
  padding: 1vh;
  border-radius: 1vh;
  box-shadow: #eae5e2 1vh 1vh;
  margin-bottom: 4vh;
  border: solid black;
}

.DontTouch {
  position: absolute;
  top: 130vh

}

.DontTouch .Content{
  height: 3vh;
}
.DontTouch .ActionPoint {
  margin: 1px;
}

.Hero2 {
  display: flex;
  align-items: center;
  background-color: #eae5e2;
  background-image: url("../Images/SleepingCats.gif");
  background-repeat: no-repeat;
  background-position-y: 68.8vh;
  background-position-x: left;
  background-size: 19vh;
  height: 75vh;
  padding: 1vh;
  border: #7cd6f9 solid 1vh;
}

.MySelfie {
  width: 30vh;
  height: 50vh;
  margin-right: 2vh;
  border-radius: 1vh;
}

.ContainerText p{
  font-size: 2vh;
  font-weight: 600;
  padding: 2vh;
  line-height: 3vh;
  width: 80vh;
  border-radius: 1vh;
  border: solid #eae5e2 1vh;
}

.Folder-Section2-left {
  right: 10vh;
}
.MessageOutline {
  border: solid red;
  width: 10vh;
}

.Instagram {
  color: rgb(222, 162, 207);
  text-decoration: none;
}

.Instagram:hover,
.Instagram:focus {
  color: #7cd6f9;
}

.Linkedin {
  color: #7cd6f9;
  text-decoration: none;
}

.Linkedin:hover,
.Linkedin:focus {
  color: rgb(222, 162, 207);
}


