.Section1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding-bottom: 1vh;
}

.Menu {
  position: fixed;
  top: .7vh;
  right: 1vh;
  font-size: 6vh;
  color: white;
  cursor: pointer;
}

.logo {
  max-width: 10vh;
}

h1 {
  color: #fff001;
  font-size: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  min-height: 100%;
}

.Name {
  color: #e6e7eb;
  font-size: 8vh;
  display: flex;
  line-height: 5vh;
  margin-bottom: 3.2vh;
  margin-top: 1.6vh;
  background-color: transparent;
}

.Position {
  line-height: 0vh;
  font-size: 1vh;
  color: #e6e7eb;
  letter-spacing: .3vh;
  margin-bottom: 3vh;
}


/* Windows *//* Windows */
/* Windows *//* Windows */

.Windows {
  height: 1vh;
}

.Window0 {
  width: 130vh;
  display: inline-block;
  margin-left:auto;
  margin-right:auto;
  left:0;
  right:0;
}

.Window0 .ActionPoint {
  margin: 1px;
}

.Window0 .Content {
  background-image: url("../Images/FlowersStamp.jpg");
  height: 43.5vh;
}

.Window1,
.Window2,
.Window3 {
  position: relative;
  bottom: 60vh;
  height: fit-content;
 }

.Window1 .Content, 
.Window2 .Content,
.Window3 .Content {
  background-size: contain;
  background-repeat: no-repeat;
}

.Window1 .Content{
  background-image: url("../Images/Mi-Image-Top-Left.png");
  background-position-y: 3vh;
  height: 2.4vh;
}

.Window2 .Content{
  background-image: url("../Images/Mi-Image-Bottom-Center.png");
  background-size: 39vh;
  height: 27.9vh;
}

.Window3 .Content{
  background-image: url("../Images/Mi-Image-Rigth.png");
  background-size: 40.7vh;
  height: 30vh;
}

.Window1 {
  bottom:64.4vh;
  left: 29.2vh;
  width: 24vh;
  z-index: 1;
}
.Window2 {
  bottom: 53.6vh;
  right: 45vh;
  z-index: 0;
}

.Window3 {
  left: 11vh;  
  z-index: 2;
}

/* Windows *//* Windows */
/* Windows *//* Windows */


@media (max-width: 1900px) {
  .Window0 {
    width: 80vw;
  }
  @media (max-width: 1200px) {
    @media (min-height: 500px) {
      .Window0 .Content {
        background-image: url("../Images/BackGround-Small.png");
        background-size: 250vw;
        background-repeat: no-repeat;
        background-position: center;
        max-height: 80vw;
      }
      .Window1,
      .Window2,
      .Window3 {
        display: none;
      }
    }
  }  
  @media (max-width: 750px) {
    @media (min-height: 750px) {
      .logo {
        max-width: 10vw;
      }
      .Name { 
        font-size: 9vw;
        margin-bottom: 4.2vw;
        margin-top: 1.6vw;
      }
      h1 {
        font-size: 4vw;
      }
      .Position {
        letter-spacing: .3vw;
        margin-bottom: 4vw;
      }
  }
  }
}