.Section3 {
  margin-top: 9vh;
  width: 85%;
}

.Section3-hoover {
  color: rgb(32, 32, 32);
  font-size: 4vh;
  background: yellow;
  border: solid black;
  padding: 1vh;
  border-radius: 1vh;
  box-shadow: #eae5e2 1vh 1vh;
  margin-bottom: 4vh;
  width: 25vh;
  text-align: center;
}

.Programs {
  display: flex;
  margin-bottom: 3vh;
}

.ImagePrograms {
  width: 5vh;
  margin:auto;
  transition: transform .2s;
  background-color: transparent;  
}

.ImagePrograms:hover,
.ImagePrograms2:hover {
  transform: scale(1.5); 
}

.Display {
  background-image: url("../Images/Background-Mini-Display.gif");
  background-size: 100vh;
  background-position-y: -35vh;
  background-position-x: -23vh;
  text-align: center;
  font-size: 2.6vh;
  resize: none;
  border-radius: 1vh;
  width: 22vh;
  height: 3vh;
}

.Display::placeholder{
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: rgb(17, 255, 0);
  padding-top: 1.5vh;
}

.Proyects {
  display: flex;
  flex-wrap: wrap;
}

.Proyect {
  width: 35vh;
  margin-top: 2vh;
  margin-bottom: 3vh;
  border-radius: 1vh;
  transition: transform .2s;
  z-index: 1000;
}

.Proyect:hover {
  transform: scale(1.1); 
}